const numberMap = {
  'all': -2,
  'hour': -2,
  '0': 0,
  'zero': 0,
  'love': 0,
  '1': 1,
  'one': 1,
  '2': 2,
  'two': 2,
  'to': 2,
  'too': 2,
  '3': 3,
  'three': 3,
  'tree': 3,
  '4': 4,
  'four': 4,
  'for': 4,
  'fall': 4,
  '5': 5,
  'five': 5,
  '6': 6,
  'six': 6,
  '7': 7,
  'seven': 7,
  '8': 8,
  'eight': 8,
  '9': 9,
  'nine': 9,
  '10': 10,
  'ten': 10,
  '11': 11,
  'eleven': 11,
  '12': 12,
  'twelve': 12,
  '13': 13,
  'thirteen': 13,
  '14': 14,
  'fourteen': 14,
  '15': 15,
  'fifteen': 15,
  '16': 16,
  'sixteen': 16,
  '17': 17,
  'seventeen': 17,
  '18': 18,
  'eighteen': 18,
  '19': 19,
  'nineteen': 19,
  '20': 20,
  'tweenty': 20,
}

export function getNumber(word) {
  const number = numberMap[word]
  if (number === null || number === undefined) {
    return -1
  }
  return number
}



